@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
:root {
  --primary: #71120b;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

body {
  font-family: "Noto Serif";
  color: #4e4e4e;
  color: var(--secondary);
}

.App {
  max-width: 960px;
  margin: 0 auto;
}

.title h2,
.title p {
  text-align: center;
}


.title p {
  padding-bottom: 2rem;
  border-bottom: 1px solid black;
}

.title h2 {
  margin-top: 60px;
  margin-bottom: 1rem;
  font-size: 2.6rem;
}

form {
  margin: 30px auto 10px;
  text-align: center;
}

label input {
  height: 0;
  width: 0;
  opacity: 0;
}

label {
  display: block;
  width: 30px;
  height: 30px;
  border: 2px solid #71120b;
  border: 2px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 30px;
  color: #71120b;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
  cursor: pointer;
}

label:hover {
  background: #71120b;
  background: var(--primary);
  color: white;
}

.output {
  height: 60px;
  font-size: 0.8rem;
}

.error {
  color: #ff4a4a;
  color: var(--error);
}

.progress-bar {
  height: 5px;
  background: #71120b;
  background: var(--primary);
  margin-top: 20px;
}

.img-grid {
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}

.img-wrap {
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  position: relative;
  opacity: 0.9;
}

.img-wrap img {
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
  border: 3px solid white;
}

